k{flex:0 0 26px;height:26px;margin-right:8px;box-sizing:border-box;box-shadow:0px 4px 8px rgba(44,39,56,0.04),0 0 0 1px #DBE2EA;border-radius:4px;cursor:pointer}.checkbox__check:before,.checkbox__check:after{display:none;position:absolute;content:"";height:2px;background:#0880AE}.checkbox__check:before{width:15px;left:8px;top:13px;transform:rotate(-45deg)}.checkbox__check:after{width:7px;left:4px;top:15px;transform:rotate(40deg)}.checkbox__check:hover{cursor:pointer}

